import axios from 'axios'

const url = '/api'

class LectureService {
    static async CreateLecture(lectureBody) {
        const response = await axios.post(url + '/lectures', lectureBody)
        return response.data
    }

    static async GetLecture(lectureId) {
        const response = await axios.get(url + '/lectures/' + lectureId)
        return response.data
    }

    static async GetHomeworks(lectureId) {
        const response = await axios.get(url + '/lectures/' + lectureId + '/homeworks')
        return response.data
    }
}

export default LectureService;