<template>
  <div>
    <v-row>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-for="(assignment, index) in assignments"
        :key="index"
      >
        <v-card dark color="primary" shaped>
          <v-card-title>
            <v-row>
              <v-col cols="1">
                <v-icon>mdi-school</v-icon>
              </v-col>
              <v-col cols="10">{{ assignment.student }}</v-col>
            </v-row>
          </v-card-title>
          <v-card-text>
              Mark: {{ assignment.mark }}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import HomeworkService from "../services/HomeworkService";
import StudentService from "../services/StudentService";
export default {
  data() {
    return {
      homework: {},
      assignments: [],
      homeworkId: Number
    };
  },
  created() {
    this.homeworkId = this.$route.params.homeworkId;

    HomeworkService.GetHomework(this.homeworkId).then(homework => {
      this.homework = homework;
    });

    HomeworkService.GetAssignments(this.homeworkId).then(assignments => {
      this.assignments = assignments;
      for (let i = 0; i < assignments.length; i++) {
        const el = assignments[i];
        StudentService.GetStudent(el.studentId).then(student => {
          el.student = student.name;
          this.assignments.splice(i, 1, el);
        });
      }
    });
  }
};
</script>

